.sideBarContainer {
    width: 20%;
    height: 100%;
    border-right: 1px solid rgba(0,0,0,.1);
}

.sideBar {
    list-style: none;
    margin: 0;
    padding: 1rem;
}

.sideBar > li > a {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #000;
    width: 100%;
    padding: 1rem;
    border-radius: 1rem;
    transition: all .5s ease-in-out;
}

.sideBar > li > a:hover {
    background: rgba(44, 168, 110, .2);
    color: #2ca86e;
}

.sideBar > li > a > svg {
    margin-right: .5rem;
    font-size: 20px;
}